<template>
    <div class="all">
        <div class="title">  <h2></h2></div>

        <div class="form-box">
            <el-form ref="form" :model="hospital" label-width="80px">

                <el-form-item label="">
                    <el-input v-model=" hospital.hname" placeholder=""></el-input>
                </el-form-item>

                <el-form-item label="">
                    <!--  <el-input type="textarea" v-model=" hospital.introduce" :rows="9"></el-input>-->
                    <div>
                        <el-upload
                            action="http://localhost:5050/uploadimg"
                            list-type="picture-card"
                            :on-preview="handlePictureCardPreview"
                            :on-remove="handleRemove"
                            :on-success="uploadSuccess">
                            <i class="el-icon-plus"></i>
                        </el-upload>
                        <el-dialog :visible.sync="dialogVisible" size="tiny">
                            <img width="100%" :src="dialogImageUrl" alt="">
                        </el-dialog>

                    </div>
                </el-form-item>

                <el-form-item label="">
                    <el-select v-model=" hospital.grade" placeholder="">
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                    </el-select>
                </el-form-item>


                <el-form-item label="">
                    <el-input type="textarea" v-model=" hospital.introduce" :rows="9"></el-input>
                </el-form-item>

                <el-form-item label="">
                   <!-- <el-input v-model=" hospital.hname" placeholder=""></el-input>-->
                <!-- <el-input type="text" id="suggestId" placeholder="" v-model="hospital.address"></el-input>-->

                    <input type="text"  style="width:515px;height: 28px" id="suggestId" placeholder="" v-model="hospital.address"></input>
               <!-- <input type="text" id="suggestId" placeholder="" v-model="hospital.address">-->
                   <div id="addressmap"></div>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" @click="onSubmit"></el-button>
                    <el-button @click="cancel"></el-button>
                </el-form-item>
            </el-form>
        </div>

    </div>
</template>

<script>
  /*  import ElInput from "../../../../node_modules/element-ui/packages/input/src/input";*/
    export default {
      /*  components: {ElInput},*/
        data: function(){
            return {
               hospital: {
                    hid: '',
                    hname:'',
                    grade:'',
                    address:'',
                    longitude:'',
                    latitude:'',
                    introduce:'',
                    picture:'',
                   address_detail: null, //
                   userlocation: {lng: "", lat: ""},
                   dialogImageUrl: '',
                   dialogVisible: false

                }
            }
        },

        //
        mounted(){
            this.$nextTick(function () {

                var th = this
                // Map
                var map = new BMap.Map("addressmap");

                //,
              // map.GeolocationControl();

                // ,
               var point = new BMap.Point(121.160724,31.173277); // 
                map.centerAndZoom(point, 15);
                map.enableScrollWheelZoom();
                var ac = new BMap.Autocomplete(    //
                    {
                        "input": "suggestId"
                        , "location": map
                    })
                var myValue
                ac.addEventListener("onconfirm", function (e) {    //
                    var _value = e.item.value;
                    myValue = _value.province + _value.city + _value.district + _value.street + _value.business;
                    this.address = myValue
                    setPlace();
                });

                function setPlace() {
                    map.clearOverlays();    //
                    function myFun() {
                        th.userlocation = local.getResults().getPoi(0).point;    //
                        map.centerAndZoom(th.userlocation, 18);
                        map.addOverlay(new BMap.Marker(th.userlocation));    //
                    }

                    var local = new BMap.LocalSearch(map, { //
                        onSearchComplete: myFun
                    });
                    local.search(myValue);

                    //
                    map.addEventListener("click",function(e){
                         //
                        console.log(th.userlocation.lng+"222222222");
                         //
                        console.log(th.userlocation.lat+"222222222");

                    })

                  //hospital
                  this.hospital.longitude=th.userlocation.lng;
                  this.hospital.latitude=th.userlocation.lat;
                }

            })
        },
        methods: {
            handleChange(value) {
                    console.log(value);
            },
            createHospital (hospital) {
              console.log(hospital.longitude+"33333333333333333");
              console.log(hospital.latitude+"44444444444444444");
                this.$axios.post(`http://localhost:5050/addHospital`, JSON.stringify(hospital),{
                    headers: {
                        'Content-Type': 'application/json;charset=UTF-8'
                    }
                });
            },
            onSubmit() {

                // userlocationhospitallongitude latitude
               /* this.hospital.longitude=this.userlocation.lng;
                this.hospital.latitude=this.userlocation.lat;*/

                console.log("test111111111");

                       this.createHospital(this.hospital), //
                this.$message.success('');

               this.$router.push({      //
                    path: '/hospitaltable'
                })

            },

            cancel(){
                this.$router.push({      //
                    path: '/hospitaltable'
                })

            },
            handleRemove(file, fileList) {
                console.log(file, fileList);
            },
            handlePictureCardPreview(file) {
                this.dialogImageUrl = file.url;
                this.dialogVisible = true;
            },
            uploadSuccess(response, file, fileList){
                console.log(response+"response1")
                this.hospital.picture=response;

            }
        }
    }
</script>


<style>
    .all{
        height: 1000px;
        width:900px;
        text-align: center;
        margin: auto;
        margin-top: 30px
    }
    .title{
        height: 20px;
        width:900px;
    }
    .form-box{
        height: 1000px;
        width:600px;
        margin: 60px auto;

    }
    #addressmap{
        width: 520px;
        height: 250px;
        margin-top: 10px;
        margin-bottom: -10px;
        border:1px solid gray;
    }
    .addressinput{
        width: 520px;
        height: 35px;
    }
</style>



