<template>
    <div class="all">
        <div class="title">  <h2></h2></div>

        <div class="form-box">
            <el-form ref="form" :model="user" label-width="80px">

                <el-form-item label="">
                    <el-input v-model="user.name" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                <el-input v-model="user.username" placeholder=""></el-input>
                </el-form-item>
              <el-form-item label="">
              <!-- <el-input v-model=" hospital.hname" placeholder=""></el-input>-->
              <el-radio-group v-model="user.gender">
                <el-radio label=""></el-radio>
                <el-radio label=""></el-radio>
                <el-radio label=""></el-radio>
              </el-radio-group>
              </el-form-item>

                <el-form-item label="">
                <el-input v-model="user.phone" placeholder=""></el-input>
                </el-form-item>

              <el-form-item label="">
                <el-input v-model="user.telephone" placeholder=""></el-input>
              </el-form-item>

              <el-form-item label="">
                <el-select v-model="user.enabled" placeholder="">
                  <el-option label="" value="0"></el-option>
                  <el-option label="" value="1"></el-option>
                </el-select>
              </el-form-item>

              <el-form-item label="">
                <el-select v-model="user.hid" placeholder="">
                  <el-option
                    v-for="item in hospitallist"
                    :key="item.hid"
                    :label="item.hname"
                    :value="item.hid">
                  </el-option>
                </el-select>
              <!--  <el-select v-model="user.hname" placeholder="">
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                </el-select>-->
              </el-form-item>
              <el-form-item label="">
                  <el-select v-model="user.role.rid" placeholder="">
                    <el-option
                      v-for="item in userrolelist"
                      :key="item.rid"
                      :label="item.nameZh"
                      :value="item.rid">
                    </el-option>
                  </el-select>
              </el-form-item>

              <el-form-item label="">
                <el-input v-model="user.address" placeholder=""></el-input>
              </el-form-item>





                <el-form-item>
                    <el-button type="primary" @click="onSubmit"></el-button>
                    <el-button @click="cancel"></el-button>
                </el-form-item>
            </el-form>
        </div>

    </div>
</template>

<script>
  /*  import ElInput from "../../../../node_modules/element-ui/packages/input/src/input";*/
    export default {
      /*  components: {ElInput},*/
        data: function(){
            return {
               user: {
                    id: '',
                    name:'',
                    phone:'',
                    telephone:'',
                    address:'',
                    enabled:'',
                    username:'',

                    hid: '',
                    hname: '',
                    role:{
                      rid:'',
                      name: '',
                      nameZh: ''
                    }
                },
              hospitallist:[],
              userrolelist:[],
              dialogImageUrl: '',
              dialogVisible: false
            }
        },
       created(){
         this.getData();
       },
        methods: {
          getData(){
            //
            this.$axios.get(`http://localhost:5050/findAllHospital`).then(data=>{
              if(data){
                this.hospitallist= data.data
                console.log(JSON.stringify(this. hospitallist)+"data2222")
              }
            })

            //role
            this.$axios.get(`http://localhost:5050/findAllUserRole`).then(data=>{
              if(data){
                this.userrolelist= data.data
                console.log(JSON.stringify(this.userrolelist)+"data2222")
              }
            })

          },
            handleChange(value) {
                    console.log(value);
            },
            createUser(user) {
                this.$axios.post(`http://localhost:5050/addUser`, JSON.stringify(user),{
                    headers: {
                        'Content-Type': 'application/json;charset=UTF-8'
                    }
                });
            },
            onSubmit() {

                // userlocationhospitallongitude latitude
               /* this.hospital.longitude=this.userlocation.lng;
                this.hospital.latitude=this.userlocation.lat;*/

                console.log("test111111111");

                       this.createUser(this.user), //
                this.$message.success('');

               this.$router.push({      //
                    path: '/usertable'
                })

            },

            cancel(){
                this.$router.push({      //
                    path: '/usertable'
                })

            },
            handleRemove(file, fileList) {
                console.log(file, fileList);
            },
            handlePictureCardPreview(file) {
                this.dialogImageUrl = file.url;
                this.dialogVisible = true;
            },
            uploadSuccess(response, file, fileList){
                console.log(response+"response1")
                this.hospital.picture=response;

            }
        }
    }
</script>


<style>
    .all{
        height: 1000px;
        width:900px;
        text-align: center;
        margin: auto;
        margin-top: 30px
    }
    .title{
        height: 20px;
        width:900px;
    }
    .form-box{
        height: 1000px;
        width:600px;
        margin: 60px auto;

    }
    #addressmap{
        width: 520px;
        height: 250px;
        margin-top: 10px;
        margin-bottom: -10px;
        border:1px solid gray;
    }
    .addressinput{
        width: 520px;
        height: 35px;
    }
</style>



