<template>
    <div class="table">
        <div class="crumbs">
          <!--  <el-breadcrumb separator="/">
                <el-breadcrumb-item><i class="el-icon-message"></i> </el-breadcrumb-item>
                <el-breadcrumb-item>  {{$route.params.hid}}&lt;!&ndash;{{this.tableData[0].hid}}&ndash;&gt;</el-breadcrumb-item>
            </el-breadcrumb>-->
        </div>
        <div class="handle-box">
            <el-input v-model="select_word" placeholder="" class="handle-input mr10"></el-input>
            <el-button type="primary" icon="search" @click="search"></el-button>
            <el-button type="primary" class="handle-insert mr10">
               <!-- <router-link :to="{name:'createdepartment',params:{hid:101}}"></router-link>-->
              <router-link to="/createuser"></router-link>
            </el-button>
        </div>
        <el-table :data="tableData" border style="width: 100%" ref="multipleTable" @selection-change="handleSelectionChange">
            <el-table-column prop="name" label="" >
            </el-table-column>
            <el-table-column prop="username" label="" >
            </el-table-column>
            <el-table-column prop="hname" label="" width="200">
            </el-table-column>
            <el-table-column prop="role.nameZh" label="">
            </el-table-column>
            <el-table-column prop="phone" label="" >
            </el-table-column>
            <el-table-column prop="telephone" label="">
            </el-table-column>
            <el-table-column prop="address" label="">
            </el-table-column>
            <el-table-column prop="enabled" label="" width="100">
            </el-table-column>

            <el-table-column label="" width="200">
                <template scope="scope">
                    <el-button size="small"
                            @click="handleEdit(scope.$index, scope.row)"><!--<router-link :to="{name:'edithospital',params:{hid:101}}"></router-link>-->
                        
                    </el-button>
                    <el-button size="small" type="danger"
                            @click="handleDelete(scope.$index, scope.row)"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination
                    @current-change ="handleCurrentChange"
                    layout="prev, pager, next"
                    :current-page="1"
                    :page-size="pagesize"
                    :total="total">
            </el-pagination>

        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                url: './static/vuetable1.json',
                //url: 'http://localhost:5050/',
                tableData: [],
                total:1000,
                hid:'',
                cur_page: 1,
                pagesize: 5,
                page:'',
                multipleSelection: [],
                select_cate: '',
                select_word: '',
                del_list: [],
                did_list:[],
                is_search: false
            }
        },
       created(){
             this.getData();
        },

       watch:{    // select_word
            select_word:function () {
                if (this.select_word == ""||this.select_word ==null) {  //
                    this.getData();
                } else {
                    this.likeselect(); //
                }
            }
        },
        methods: {

          handleCurrentChange(val){
                this.cur_page = val;
                console.log(this.cur_page+"this.cur_page555555555555555555");

               this.getData();


            },
            getData(){
                this.$axios.get(`http://localhost:5050/findAllSystemManagerUserPage?page=${this.cur_page}&size=${this.pagesize}`).then(data=>{
                    if(data){
                        console.log(3333333333333333333333333333333333333333333333333333333);
                        console.log(JSON.stringify(data.data.pageuserdata));
                        console.log(data.data.total+"total");
                        this.total=data.data.total;
                        this.tableData=data.data.pageuserdata;
                    }
                })

            },
            insertDepartment(){
                this.$router.push({
                    path: '/createdepartment'
                })

            },

            search(){  //

               //
                this.likeselect();


            },

            likeselect(){
                //
                this.$axios.get(`http://localhost:5050/getUserLike/${this.select_word}`).then(data=> {
                    if (data) {
                        this.tableData = data.data;
                    }
                })
            },
           handleEdit(index, row) {
               console.log(row.id+"row.didtest------------");
           /*    console.log(row.did+"row.hidtest------------");*/
                this.$router.push({
                  /*path: '/editdoctor'*/
                     name: 'edituser',
                     params: {
                         id: row.id
                    }
                })

            },
            handleDelete(index, row) {
                console.log(index+"index");
                console.log(row.id+"row");
                this.$axios.delete(`http://localhost:5050/deleteUser/${row.id}`).then(response=>{
                    const status = response.status;
                    console.log(status+"status");
                    if(status===200){
                        this.$message.success(''+(row.id)+'');
                        this.getData();
                    }else{
                        this.$message.success(''+(row.id)+'');
                    }
                });


            },
            delAll(){
                const self = this,
                    length = self.multipleSelection.length; //self.multipleSelection

                let str = '';                 //str,$message
                self.del_list = self.del_list.concat(self.multipleSelection);
                for (let i = 0; i < length; i++) {
                    str += self.multipleSelection[i].did + ' ';    //concatmultipleSelectiondel_list
                    self.did_list.push(self.multipleSelection[i].did);     //multipleSelectiondid,
                }

                //hid_list
                this.batchdelete(self.did_list,str);

            },

            batchdelete(idlist,str){
                this.$axios.post(`http://localhost:5050/batchdeleteDepartment`,JSON.stringify(idlist),{
                    headers: {
                        'Content-Type': 'application/json;charset=UTF-8'
                    }

                }).then(response=>{
                    const status = response.status;
                    console.log(status+"status1111111111111111111111111");
                   if(status===200){
                        this.$message.success(''+str+'');
                        this.getData();
                    }
                });
            },

            handleSelectionChange(val) {     //
                this.multipleSelection = val;
            }
        }
    }
</script>

<style scoped>
.handle-box{
    margin-top: 50px;
  /*  margin-left:400px;*/
    margin-bottom: 20px;
}
.handle-insert{
    margin-left: 560px;
}
.handle-select{
    width: 120px;
}
.handle-input{
    width: 380px;
    display: inline-block;
}
</style>



